exports.push([module.id, "\n.query-form{\n    margin: 0 auto;\n    width: 460px;\n}\n", "", {"version":3,"sources":["/./src/pages/admin/subpages/ManageCard.vue?147e82f6"],"names":[],"mappings":";AA2IA;IACA,eAAA;IACA,aAAA;CACA","file":"ManageCard.vue","sourcesContent":["<template>\n    <div style=\"text-align: center\">\n        <h1>借书证管理</h1>\n        <div>\n            <el-tabs v-model=\"activeName\" @tab-click=\"handleClick\">\n                <el-tab-pane label=\"增加借书证\" name=\"first\">\n                    <el-form\n                            class=\"query-form\"\n                            ref=\"form\"\n                            :model=\"form\"\n                            label-width=\"100px\">\n                        <el-form-item label=\"姓名\">\n                            <el-input v-model=\"form.name\"></el-input>\n                        </el-form-item>\n                        <el-form-item label=\"单位\">\n                            <el-input v-model=\"form.unit\"></el-input>\n                        </el-form-item>\n                        <el-form-item label=\"类别\">\n                            <el-input v-model=\"form.type\"></el-input>\n                        </el-form-item>\n                        <el-form-item>\n                            <el-button type=\"primary\" @click=\"onAdd\">增加</el-button>\n                        </el-form-item>\n                    </el-form>\n                </el-tab-pane>\n                <el-tab-pane label=\"借书证管理\" name=\"second\">\n                    <el-table\n                            v-loading.body=\"result == null && activeName == 'second'\"\n                            :data=\"result != null ? result : []\"\n                            border\n                            :fit=\"true\"\n                            style=\"width: 100%\"\n                            :default-sort = \"{prop: 'name', order: 'descending'}\"\n                    >\n                        <el-table-column\n                                prop=\"cardid\"\n                                label=\"借书证编号\"\n                                sortable>\n                        </el-table-column>\n                        <el-table-column\n                                prop=\"name\"\n                                label=\"姓名\"\n                                sortable\n                                width=\"100\">\n                        </el-table-column>\n                        <el-table-column\n                                prop=\"unit\"\n                                label=\"单位\"\n                                sortable\n                                width=\"100\">\n                        </el-table-column>\n                        <el-table-column\n                                prop=\"type\"\n                                label=\"类别\"\n                                sortable\n                                width=\"100\">\n                        </el-table-column>\n                        <el-table-column label=\"操作\">\n                            <template scope=\"scope\">\n                                <el-button\n                                        size=\"small\"\n                                        type=\"danger\"\n                                        @click=\"handleDelete(scope.$index, scope.row)\">删除</el-button>\n                            </template>\n                        </el-table-column>\n                    </el-table>\n                </el-tab-pane>\n            </el-tabs>\n            <el-alert\n                    v-if=\"status == 'success'\"\n                    :title=\"statusInfo\"\n                    type=\"success\">\n            </el-alert>\n            <el-alert\n                    v-if=\"status == 'failed'\"\n                    :title=\"statusInfo\"\n                    type=\"error\">\n            </el-alert>\n        </div>\n    </div>\n</template>\n<script>\n    import {Network} from \"../../../network\";\n    export default {\n        data(){\n            return {\n                form :{\n                    name: '',\n                    unit: '',\n                    type: ''\n                },\n                result : null,\n                activeName: 'first',\n                status: null,\n                statusInfo: ''\n            }\n        },\n        mounted(){\n            this.loadCards();\n        },\n        methods: {\n            loadCards(){\n                const that = this;\n                Network.get('/admin/card',(result)=>{\n                    that.result = result;\n                },(message)=>{\n                    that.status = 'failed';\n                    that.statusInfo = message;\n                });\n            },\n            onAdd(){\n                const that = this;\n                Network.put(`/admin/card?name=${this.form.name}&type=${this.form.type}&unit=${this.form.unit}`,{},(ret)=>{\n                    that.loadCards();\n                    that.status = 'success';\n                    that.statusInfo = `success!: cardid:${ret[\"code\"]}`;\n                },(message)=>{\n                    that.status = 'failed';\n                    that.statusInfo = message;\n                })\n            },\n            handleClick(){\n                this.status = '';\n            },\n            handleDelete(ix, row){\n                const that = this;\n                Network.del(`/admin/card?cardid=${row.cardid}`,()=>{\n                    that.loadCards();\n                    that.status = 'success';\n                    that.statusInfo = 'success';\n                },(message)=>{\n                    that.status = 'failed';\n                    that.statusInfo = message;\n                })\n            }\n        }\n    }\n</script>\n<style>\n    .query-form{\n        margin: 0 auto;\n        width: 460px;\n    }\n</style>"],"sourceRoot":"webpack://"}]);

