exports.push([module.id, "\n.query-form{\n    margin: 0 auto;\n    width: 460px;\n}\n", "", {"version":3,"sources":["/./src/pages/admin/subpages/ManageAdmin.vue?7a7a9395"],"names":[],"mappings":";AAwIA;IACA,eAAA;IACA,aAAA;CACA","file":"ManageAdmin.vue","sourcesContent":["<template>\n    <div style=\"text-align: center\">\n        <h1>管理员管理</h1>\n        <div>\n            <el-tabs v-model=\"activeName\" >\n                <el-tab-pane label=\"增加管理员\" name=\"first\">\n                    <el-form\n                            class=\"query-form\"\n                            ref=\"form\"\n                            :model=\"form\"\n                            label-width=\"100px\">\n                        <el-form-item label=\"用户名\">\n                            <el-input v-model=\"form.username\"></el-input>\n                        </el-form-item>\n                        <el-form-item label=\"姓名\">\n                            <el-input v-model=\"form.name\"></el-input>\n                        </el-form-item>\n                        </el-form-item>\n                        <el-form-item label=\"联系方式\">\n                            <el-input v-model=\"form.contact\"></el-input>\n                        </el-form-item>\n                        <el-form-item label=\"密码\">\n                            <el-input\n                                    type=\"password\"\n                                    v-model=\"form.password\"></el-input>\n                        </el-form-item>\n                        <el-form-item>\n                            <el-button type=\"primary\" @click=\"onAdd\">增加</el-button>\n                        </el-form-item>\n                    </el-form>\n                </el-tab-pane>\n                <el-tab-pane label=\"管理员管理\" name=\"second\">\n                    <el-table\n                            v-loading.body=\"result == null && activeName == 'second'\"\n                            :data=\"result != null ? result : []\"\n                            border\n                            :fit=\"true\"\n                            style=\"width: 100%\"\n                            :default-sort = \"{prop: 'name', order: 'descending'}\"\n                    >\n                        <el-table-column\n                                prop=\"username\"\n                                label=\"用户名\"\n                                sortable>\n                        </el-table-column>\n                        <el-table-column\n                                prop=\"name\"\n                                label=\"姓名\"\n                                sortable\n                                width=\"100\">\n                        </el-table-column>\n                        <el-table-column\n                                prop=\"contact\"\n                                label=\"联系方式\"\n                                sortable\n                                width=\"100\">\n                        </el-table-column>\n                        <el-table-column label=\"操作\">\n                            <template scope=\"scope\">\n                                <el-button\n                                        size=\"small\"\n                                        type=\"danger\"\n                                        @click=\"handleDelete(scope.$index, scope.row)\">删除</el-button>\n                            </template>\n                        </el-table-column>\n                    </el-table>\n                </el-tab-pane>\n            </el-tabs>\n            <el-alert\n                    v-if=\"status == 'success'\"\n                    :title=\"statusInfo\"\n                    type=\"success\">\n            </el-alert>\n            <el-alert\n                    v-if=\"status == 'failed'\"\n                    :title=\"statusInfo\"\n                    type=\"error\">\n            </el-alert>\n        </div>\n    </div>\n</template>\n<script>\n    import {Network} from \"../../../network\";\n    export default {\n        data(){\n            return {\n                form :{\n                    username: '',\n                    password: '',\n                    name: '',\n                    contact: ''\n                },\n                result : null,\n                activeName: 'first',\n                status: '',\n                statusInfo: ''\n            }\n        },\n        mounted(){\n            this.loadAdmins();\n        },\n        methods: {\n            loadAdmins(){\n                const that = this;\n                Network.get('/admin/admin',(result)=>{\n                    that.result = result;\n                });\n            },\n            onAdd(){\n                const that = this;\n                Network.put(`/admin/admin?username=${this.form.username}&password=${this.form.password}&name=${this.form.name}&contact=${this.form.contact}`,{},(ret)=>{\n                    console.log(ret);\n                    that.loadAdmins();\n                    that.status = 'success';\n                    that.statusInfo = 'success';\n                },(message)=>{\n                    that.status = 'failed';\n                    that.statusInfo = message;\n                })\n            },\n            handleDelete(ix, row){\n                const that = this;\n                Network.del(`/admin/admin?username=${row.username}`,(ret)=>{\n                    that.loadAdmins();\n                    that.status = 'success';\n                    that.statusInfo = 'success';\n                },(message)=>{\n                    that.status = 'failed';\n\n                    that.statusInfo = message;\n                })\n            }\n        }\n    }\n</script>\n<style>\n    .query-form{\n        margin: 0 auto;\n        width: 460px;\n    }\n</style>"],"sourceRoot":"webpack://"}]);

