button>\n            </el-form-item>\n        </el-form>\n        <div v-else>\n            <div>\n                <el-button type=\"primary\" @click=\"onReturn\">返回</el-button>\n            </div>\n            <el-table\n                    v-loading.body=\"result == null\"\n                    :data=\"result != null ? result : []\"\n                    border\n                    fit=\"true\"\n                    style=\"width: 100%\"\n                    :default-sort = \"{prop: 'name', order: 'descending'}\"\n            >\n                <el-table-column\n                        prop=\"bookid\"\n                        label=\"书号\"\n                        sortable>\n                </el-table-column>\n                <el-table-column\n                        prop=\"name\"\n                        label=\"书名\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n                <el-table-column\n                        prop=\"type\"\n                        label=\"类型\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n                <el-table-column\n                        prop=\"press\"\n                        label=\"出版社\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n                <el-table-column\n                        prop=\"year\"\n                        label=\"年份\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n                <el-table-column\n                        prop=\"author\"\n                        label=\"作者\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n                <el-table-column\n                        prop=\"price\"\n                        label=\"价格\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n                <el-table-column\n                        prop=\"count\"\n                        label=\"库存量\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n                <el-table-column\n                        prop=\"current\"\n                        label=\"余量\"\n                        sortable\n                        width=\"100\">\n                </el-table-column>\n            </el-table>\n\n        </div>\n    </div>\n</template>\n<script>\n    import {Network} from \"../../network\";\n    export default {\n        data(){\n            return {\n                showForm: true,\n                form :{\n                    type : '',\n                    name : '',\n                    yearLeft: '',\n                    yearRight: '',\n                    priceLeft: '',\n                    priceRight: '',\n                    author : '',\n                    press : ''\n                },\n                result: null\n            }\n        },\n        methods :{\n            onQuery(){\n                const queryString =\n                    Object.keys(this.form)\n                        .filter(x=>this.form[x])\n                        .map(x=>`${x}=${this.form[x]}`)\n                        .join('&');\n                this.result = null;\n                this.showForm = false;\n                Network.get('/query?'+queryString,(result)=>{\n                    this.result = result;\n                });\n            },\n            onReturn(){\n                this.result = null;\n                this.showForm = true;\n            }\n        }\n    }\n</script>\n<style>\n    .query-form{\n        margin: 0 auto;\n        width: 460px;\n    }\n</style>"],"sourceRoot":"webpack://"}]);

